import React from "react";
import { useRef, useEffect } from "react";
import useSize from "./useSize";
import "./canvas.scss";

//    ,     
//     canvas.
function animateBars(analyser, canvas, canvasCtx, dataArray, bufferLength) {
    //      `getByteFrequencyData` API -.
    analyser.getByteFrequencyData(dataArray);

    //     .
    canvasCtx.fillStyle = "#000";

    //   .
    const HEIGHT = canvas.height / 2;

    //             .
    var barWidth = Math.ceil(canvas.width / bufferLength) * 2.5;

    //       x-.
    let barHeight;
    let x = 0;

    //     `dataArray`.
    for (var i = 0; i < bufferLength; i++) {
        //          .
        barHeight = (dataArray[i] / 255) * HEIGHT;
        const blueShade = Math.floor((dataArray[i] / 255) * 5); //       *5     
        // const blueHex = ["#61dafb", "#5ac8fa", "#50b6f5", "#419de6", "#20232a"][blueShade]; //     React
        // const blueHex = ["#ffffff", "#f3f368", "#ffff00", "#ff7300", "#ff0000"][blueShade]; //     
        //  const blueHex = ["#20232a", "#ffffff", "#ffffff", "#ffffff", "#20232a"][blueShade]; //     
         const blueHex = ["#50b6f5","#008000", "#86c528",  "#0000ff", "#d400ff"][blueShade]; //   
        //        RGB.
        canvasCtx.fillStyle = blueHex;

        //        x      .
        canvasCtx.fillRect(x, HEIGHT - barHeight, barWidth, barHeight);

        //   x   .
        x += barWidth + 1;
    }
}
// Component to render the waveform
const AudioWaveForm = React.memo(({ analyzerData }) => {
    //    canvas
    const canvasRef = useRef(null);
    const { dataArray, analyzer, bufferLength } = analyzerData;
    const [width, height] = useSize();

    //   
    const draw = (dataArray, analyzer, bufferLength) => {
        const canvas = canvasRef.current;
        if (!canvas || !analyzer) return;
        const canvasCtx = canvas.getContext("2d");

        const animate = () => {
            requestAnimationFrame(animate);
            // eslint-disable-next-line no-self-assign
            canvas.width = canvas.width;
            canvasCtx.translate(0, canvas.offsetHeight / 2 - 115);
            animateBars(analyzer, canvas, canvasCtx, dataArray, bufferLength);
        };

        animate();
    };
    //         
    useEffect(() => {
        draw(dataArray, analyzer, bufferLength);
    }, [dataArray, analyzer, bufferLength]);

    return <canvas className="canvas" ref={canvasRef} width={width} height={height} />;
});

export default AudioWaveForm;
